* {
  text-align: center;
  margin: 0px;
  padding: 1px;
}
.App{
  background-color: antiquewhite;
  height: 100vh;
  width: 97.7vw;
  padding: auto;
  
}
.inputBox{
  height: 30vh;
  background-color: rgb(230, 213, 190);
  display: flex;
  flex-direction: row;
align-items: center;
justify-content: center;
}
.heading{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-shadow: 2px 2px 9px  #46918d;
  color: rgb(185, 161, 131);
}
.inputBox input{
  width: 35vw;
  height: 4vh;
  background-color: rgb(209, 207, 207);
  border-radius: 5px;
  font-size: 2.7vh;
}

.inputBox button {
  width: 15vw;
  height: 5vh;
  margin-left:2vw ;
  border-radius: 4px;
  font-size: 2.5vh;
  background: rgb(170, 233, 252);
  -webkit-transition: background-color 2s ease-out;
  -moz-transition: background-color 2s ease-out;
  -o-transition: background-color 2s ease-out;
  transition: background-color 2s ease-out;
}

.inputBox button:hover {
  background-color: rgb(136, 252, 171);
  cursor: pointer;
}
.delete{
  background-color: #83c0bd;
  width: 1.9vw;
  height: 4vh;
  border-radius: 50%;
}
.TodoList{
  display: flex;
  flex-direction: column;
  
align-items: center;
justify-content: center;
  /* text-align: center; */
  width: 60vw; 
}
/* table {
  width: 100%;
} */
@media (max-width: 990px) {
  
  .inputBox button {
    width: 25vw;
    font-size: 2vh;
  }
  .inputBox input{
    width: 25vw;
    font-size: 2vh;
  }
}
@media (max-width: 600px) {
  .inputBox {
    flex-direction: column;
    flex: 100%;
    font-size: 2vh;
  }
  .inputBox button {
    width: 25vw;
    font-size: 2vh;
  }
  .inputBox input{
    width: 25vw;
    font-size: 2vh;
  }
}